import { AudioMng, AudioObj } from "../../AudioMng/index_AudioMng";

const {ccclass, property} = cc._decorator;

@ccclass
export default class NewClass extends cc.Component {

	/*== Constructer ============================================= */

	/*== Static ===================================================*/

	/*== Member ===================================================*/

	/*== Event ====================================================*/

	/*== Cocos LifeCycle ==========================================*/

	// LIFE-CYCLE CALLBACKS:

	// onLoad () {}

	start () {

		AudioMng.play('bgm');

		let layer1 = AudioMng.layer('maingame');
		layer1.add('bgm').vol(1).prio(2);
		cc.log(1);

		let layer2 = AudioMng.layer('freegame');
		layer2.add('bgm').vol(0).prio(1);
		cc.log(2);

		this.scheduleOnce(()=>{
			let layer3 = AudioMng.layer('bonusgame');
			layer3.add('bgm').vol(0).prio(3);
			cc.log(3);
		}, 3);

		this.scheduleOnce(()=>{
			layer2.add('bgm').vol(1).prio(4);
			cc.log(4);
		}, 6);

		this.scheduleOnce(()=>{
			AudioMng.audio('bgm').setVolume(0.5);
			cc.log(5);
		}, 9);


		this.scheduleOnce(()=>{
			AudioMng.rmlayer('freegame');
			cc.log(6);
		}, 12);

		

	}

	update (dt) {
		
		
	}

	
	/*== Public Function ==========================================*/

	/* 呼叫 */
	// public call (var1: Function, var2: number) : void {

	// }
	
	/*== Protected Function =======================================*/

	/*== Private Function =========================================*/


}

